import React, { Component } from 'react';

class ErrorBoundry extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false,
            msg: ''
        };
    }

    componentDidCatch() {
        this.setState({ hasError: true });
    }

    render() {
        const hasError = this.state.hasError || this.props.hasError;
        const msg = this.props.hasError ? this.props.msg : '';
        return hasError ?
            (
                <div>
                    <h1>Oooops. That is not good!</h1>
                    <h2 className='washed-yellow'>{msg}</h2>
                </div>
            ) :
            this.props.children;
    }
}

export default ErrorBoundry;



